@import 'variables.scss';

.profile-intro {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 24px;
  .title {
    @include subtitle1
  }
  .subtitle {
    @include caption;
    color: $caption;
  }
}

.img-wrapper {
  position: relative;
  margin: 24px;
  .profile-img {
    width: 118px;
    height: 118px;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  label {
    position: absolute;
    right: 0;
    bottom: 0;
    cursor: pointer;
    width: 36px;
    height: 36px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: #fff;
  }

  #upload-photo {
    display: none;
    z-index: -1;
  }
}
