$violet700: #3700B3;
$violet500: #35037b;
$violet200: #BB86FC;
$teal200: #03DAC6;
$red700: #B00020;
$red200: #CF6679;
$white: #FFFFFF;
$gray200: #F5F5F5;
$gray900: #121212;
$black: #000000;
$caption: #666666;
$gray-btn: #E5E5E5;

@mixin headline3 {
  font-size: 48px;
  line-height: 64px;
}
@mixin headline4 {
  font-size: 34px;
  line-height: 40px;
}
@mixin headline5 {
  font-size: 24px;
  line-height: 32px;
}
@mixin headline6 {
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: 0.15px;
}
@mixin subtitle1 {
  font-size: 16px;
  line-height: 24px;
}
@mixin subtitle2 {
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
}
@mixin button {
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  text-transform: uppercase;
}
@mixin body1 {
  font-size: 16px;
  line-height: 24px;
}
@mixin body2 {
  font-size: 14px;
  line-height: 20px;
}
@mixin caption {
  font-size: 12px;
  line-height: 16px;
}
@mixin overline {
  font-size: 10px;
  line-height: 16px;
  text-transform: uppercase;
}
