@import "variables";

.media {
  .photos {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .buttons {
    display: flex;
    justify-content: space-between;
    label {
      text-align: center;
      padding: 5px;
    }
    #upload-photo {
      display: none;
      z-index: -1;
    }
  }
  .main-btn {
    display: inline-block;
    width: 156px;
    height: 36px;
    font-family: inherit;
    @include button;
    border:none;
    border-radius: 4px;
    text-decoration: none;
    background-color: #fff;
    &.violet {
      background-color: $violet500;
      color: #fff;
      &:hover {
        background-color: #22024e;
      }
      &:active {
        transform: scale(0.98);
      }
    }
    &.white {
      background-color: #fff;
      color: $violet500;
      border: 1px solid $gray-btn;
      &:hover {
        background-color: #e0dcdc;
      }
      &:active {
        transform: scale(0.98);
      }
    }
  }
}
