@import './styles/variables.scss';

.section-title {
  @include subtitle1;
  margin-bottom: 5px;
}
hr {
  height: 1px;
  background-color: rgba(33, 33, 33, 0.08);
  border: none;
  margin: 0 auto;
}

section {
  padding: 16px;
}

.App {
  &.dark-mode {
    background-color: #121212;
    color: white;
    a {
      color: white;
    }
    section {
      background-color: #1e1e1e;
      margin: 0 10px;
      margin-bottom: 10px;
      border-radius: 10px;
    }
  }
}
