@import "variables";

.friend-wrapper {
  position: relative;
  hr {
    position: absolute;
    right: -16px;
    width: 87.5%;
  }
}
.friend {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  height: 76px;
  .profile-img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  .about {
    margin: 0 16px;
    .title {
      @include subtitle1
    }
    .subtitle {
      @include caption;
      color: $caption;
    }
  }
  .rm-friend-btn {
    position: absolute;
    right: 0;
  }
}
