@import 'variables';

.add-friend-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 36px;
  width: 100%;
  color: #000;
  background-color: #fff;
  text-decoration: none;
  @include button;
  border: 2px solid $gray-btn;
  border-radius: 4px;
  cursor: pointer;
  transition: all .1s linear;
  &:hover {
    background-color: #e0dcdc;
  }
  &:active {
    transform: scale(0.98);
  }
}
